// OVERLAY

<template>
    <div id="container">
        <div class="flex">
            <h2 class="subMenu" :class="{ chosen: state.visibleContent===0 }" @click="toggleVisibility(0)">MATTER.JS</h2>
            <h2 class="subMenu" :class="{ chosen: state.visibleContent===1 }" @click="toggleVisibility(1)">PHASE 1</h2>
            <h2 class="subMenu" :class="{ chosen: state.visibleContent===2 }" @click="toggleVisibility(2)">PHASE 2</h2>
            <h2 class="subMenu" :class="{ chosen: state.visibleContent===3 }" @click="toggleVisibility(3)">SUMMARY</h2>
            <h2 class="subMenu" :class="{ chosen: state.visibleContent===4 }" @click="toggleVisibility(4)">SAVE PDF</h2>
        </div>
        <div class="flex secondMenu">
            <h2 class="subMenu" :class="{ chosen: state.visibleContent===5 }" @click="toggleVisibility(5)">LOGIN</h2>
            <h2 class="subMenu" :class="{ chosen: state.visibleContent===6 }" @click="toggleVisibility(6)">USER SERVICE</h2>
            <h2 class="subMenu" :class="{ chosen: state.visibleContent===7 }" @click="toggleVisibility(7)">ROUTER</h2>
            <h2 class="subMenu" :class="{ chosen: state.visibleContent===8 }" @click="toggleVisibility(8)">DBMETHODS</h2>
            <h2 class="subMenu" :class="{ chosen: state.visibleContent===9 }" @click="toggleVisibility(9)">MYSQL</h2>
        </div>
        <div class="content" v-if="state.visibleContent===0">
            <p class="contentText">Projektin alkuvaiheessa suunnittelimme sovellukseen fysiikkapohjaisia Trigenda-kortteja.
            Löysimme Matter.js kirjaston, jonka toimintaa testasin Vue.js ympäristössä.</p>
            <iframe src="https://player.vimeo.com/video/538002236?badge=0&amp;autopause=0&amp;player_id=0&amp;app_id=58479" width="640" height="360" frameborder="0" allow="autoplay; fullscreen; picture-in-picture" allowfullscreen title="Matter.js testi"></iframe>
            <p class="preFormatted" v-html="state.matterjs"></p>
        </div>
        <div class="content" v-if="state.visibleContent===1">
            <p class="contentText">Projektin alussa kehitetty versio keskustelun ensimmäisestä vaiheesta, jota kutsuimme nimellä Phase 1.
            Ensimmäisessä vaiheessa käyttäjä lajittelee valitun Trigenda korttisarjan jokaisen kortin haluamaansa kategoriaan.
            Kategoriat ovat Voimavara, Ongelma, Haaste ja Toive. Ulkoasu on vielä karu. Korttivalinnat tallentuvat selaimen LocalStorageen.</p>
            <iframe src="https://player.vimeo.com/video/538531432?badge=0&amp;autopause=0&amp;player_id=0&amp;app_id=58479" width="640" height="360" frameborder="0" allow="autoplay; fullscreen; picture-in-picture" allowfullscreen title="Phase 1 Old"></iframe>
            <p class="preFormatted" v-html="state.phase1backup1"></p>
            <p class="contentText">Viimeisimmässä Demossa ulkoasu on hioutunut paljon modernimmaksi ja LocalStorageen tallennettavat
            tiedot on jäsennelty olioihin paremmin.</p>
            <iframe src="https://player.vimeo.com/video/538531641?badge=0&amp;autopause=0&amp;player_id=0&amp;app_id=58479" width="640" height="360" frameborder="0" allow="autoplay; fullscreen; picture-in-picture" allowfullscreen title="Phase 1 and 2"></iframe>  
            <p class="preFormatted" v-html="state.phase1demo2"></p>
        </div>
        <div class="content" v-if="state.visibleContent===2">
            <p class="contentText">Phase 2 vaiheessa valitaan keskusteluun kortteja pakoista, jotka muodostuvat Phase 1 valintojen perusteella.
            Keskusteluun valitaan Tavoite, Haaste ja kaksi Voimavaraa. Varhaisemmassa ohjelmistoversiossa pakan valinta ja korttivalinta tehdään Overlay komponentissa.
            Overlay komponentissa käyttäjä antaa myös tekstimuotoiset perustelut korttivalinnoista. Tiedot tallennetaan selaimen LocalStorageen.
            <br><br>
            Vanhemmassa sovellusversiossa Overlay komponentissa on käytetty useita CardTypeSelection, 
            CardSelection ja Answer komponentteja, jotka näytetään Overlayn progressin mukaan. Tämä on kuitenkin tarpeetonta koodin 
            toistamista.
            </p>
            <iframe src="https://player.vimeo.com/video/538547230?badge=0&amp;autopause=0&amp;player_id=0&amp;app_id=58479" width="640" height="360" frameborder="0" allow="autoplay; fullscreen; picture-in-picture" allowfullscreen title="Phase 2 Old Overlay"></iframe>
            <p class="preFormatted" v-html="state.phase2old"></p>
            <p class="preFormatted" v-html="state.overlayold"></p>
            <p class="contentText">Uudemmassa versiossa pakka valitaan Phase 2 komponentissa ja ainoastaan korttivalinta ja vastausteksti
            tehdään Overlay komponentissa. Overlay:ssä on myös vähemmän koodia ja sen templaatissa Child komponentteja käytetään
            nyt järkevämmin uudelleen ylimääräisen koodin välttämiseksi.
            <br><br>
            Uudemmassa sovellusversiossa Overlay komponentin templaatissa on vain yksi CardSelection ja Answer 
            komponentti, joita hyödynnetään jokaisen kortin valintaan. CardTypeSelectionin toiminta on siirretty 
            kokonaan Phase 2 komponenttiin.</p>
            <iframe src="https://player.vimeo.com/video/538531471?badge=0&amp;autopause=0&amp;player_id=0&amp;app_id=58479" width="640" height="360" frameborder="0" allow="autoplay; fullscreen; picture-in-picture" allowfullscreen title="Phase 2 New Overlay"></iframe>
            <p class="preFormatted" v-html="state.phase2demo2"></p>
            <p class="preFormatted" v-html="state.overlaydemo2"></p> 
        </div>
        <div class="content" v-if="state.visibleContent===3">
            <p class="contentText">Summary komponentissa keskustelija kirjoittaa oman yhteenvetonsa keskustelusta. Kysymyksiä on muutama
            ja jokaiseen kysymykseen annetaan oma vastauksensa. Jo annettua vastausta pääsee myös halutessaan muokkaamaan.
            Annetut vastaustekstit tallentuvat selaimen LocalStorageen.</p>
            <iframe src="https://player.vimeo.com/video/538531522?badge=0&amp;autopause=0&amp;player_id=0&amp;app_id=58479" width="640" height="360" frameborder="0" allow="autoplay; fullscreen; picture-in-picture" allowfullscreen title="Summary"></iframe>
            <p class="preFormatted" v-html="state.summarydemo2"></p>
            <p class="preFormatted" v-html="state.summaryanswer"></p>
        </div>
        <div class="content" v-if="state.visibleContent===4">
            <p class="contentText">Keskustelun lopussa käyttäjä voi tallentaa keskustelun yhteenvedon PDF-tiedostoon.
            PDF-tiedoston sisältö luodaan LocalStorageen tallennetuista käyttäjän valinnoista. PDF-tiedoston luomiseen on käytetty JSPDF-kirjastoa.</p>
            <iframe src="https://player.vimeo.com/video/538531597?badge=0&amp;autopause=0&amp;player_id=0&amp;app_id=58479" width="640" height="360" frameborder="0" allow="autoplay; fullscreen; picture-in-picture" allowfullscreen title="Save PDF"></iframe>
            <p class="preFormatted" v-html="state.savepdfdemo2"></p>  
        </div>
        <div class="content" v-if="state.visibleContent===5">
            <p class="contentText">Käyttäjä voi kirjautua sovellukseen Login komponentissa. Backend palauttaa JWT-tokenin, mikäli
            käyttäjätunnus ja salasana ovat oikein. Backend tarkistaa käyttäjätunnuksen ja salasanan tietokannasta.<br><br>
            Kirjautuneen käyttäjän tunnus tallennetaan reaktiiviseen Vuex Storeen, joka ylläpitää sovelluksen tilanhallintaa.
            Kun käyttäjä kirjautuu ulos sovelluksesta, asetetaan Vuex Storen loggedUser arvoksi null.
            Navbar (navigointipalkki) komponentti näyttää Vuex Storen loggedUser:in arvon perusteella sisäänkirjautumislinkin
            tai kirjautuneen käyttäjän tunnuksen.</p>
            <iframe src="https://player.vimeo.com/video/538531354?badge=0&amp;autopause=0&amp;player_id=0&amp;app_id=58479" width="640" height="360" frameborder="0" allow="autoplay; fullscreen; picture-in-picture" allowfullscreen title="Login"></iframe>
            <p class="preFormatted" v-html="state.logindemo2"></p>
            <p class="contentText">Vuex store on luotu omaan store.js tiedostoon. Vue projektissa se otetaan käyttöön main.js tiedostossa.
            Store:n state olioon on tallennettu reaktiiviset muuttujat. Mutations olioon on tallennettu funktiot, joilla voidaan muuttaa
            state olion muuttujien arvoja.</p>
            <p class="preFormatted" v-html="state.store"></p> 
            <p class="preFormatted" v-html="state.main"></p> 
        </div>
        <div class="content" v-if="state.visibleContent===6">
            <p class="contentText">User Servicessä on funktio Backendin kanssa keskustelua varten.
            processCredentials funktiota voidaan käyttää uuden käyttäjän rekisteröintiin
            tai jo olemassa olevan käyttäjän sisäänkirjaamiseen. Frontend lähettää kirjautumis- tai
            rekisteröintipyynnön Backendille Axios kirjaston avulla.</p>
            <p class="preFormatted" v-html="state.userservicedemo2"></p>  
        </div>
        <div class="content" v-if="state.visibleContent===7">
            <p class="contentText">Routerista löytyy Backendin reitit. Siellä käsitellään Frontendistä tulleet uuden käyttäjän
            rekisteröintipyynnöt ja olemassa olevan käyttäjän kirjautumispyynnöt. Uuden käyttäjän tiedot tallennetaan tietokantaan.
            Salasana kryptataan bcrypt kirjaston avulla tietoturva syistä.<br><br>
            Jos käyttäjä syöttää oikean käyttäjätunnuksen ja salasanan sisäänkirjautuessaan, Backend lähettää Frontendille JWT-tokenin.
            Mikäli käyttäjätunnus tai salasana on väärä, Backend lähettää Frontendille virheilmoituksen.</p>
            <p class="preFormatted" v-html="state.backendrouterdemo2"></p>  
        </div>
        <div class="content" v-if="state.visibleContent===8">
            <p class="contentText">Sovelluksessa käytetään MySQL-tietokantaa. Dbmethods pitää sisällään funktiot MySQL-queryjä varten.
            Funktioille annetaan myös tarvittavat parametrit.</p>
            <p class="preFormatted" v-html="state.dbmethodsdemo2"></p>  
        </div>
        <div class="content" v-if="state.visibleContent===9">
            <p class="contentText">TrigendaDigi sovellukseen luotiin MySQL-tietokanta. Tässä ensimmäinen versio
            tietokannan rakenteesta. Eri käyttäjätasot ovat omina tauluinaan, mikä ei ole paras mahdollinen ratkaisu.</p>
            <img class="sqlImage" src="../code/sql/databaseV1.png" />
            <p class="contentText">Tietokannan toisessa versiossa eri käyttäjätasojen tallentamiseen käytetään yhtä taulua (User).
            Keskustelun Phase 1, Phase 2 ja Feedback korttien tallentamiseen käytetään samaa taulua. Parempi ratkaisu olisi
            tehdä keskustelun eri vaiheiden korttien tallentamiseen omat taulut. License ja Deck taulujen yhdistäminen ei ole
            vielä valmis.</p>
            <img class="sqlImage" src="../code/sql/databaseV2.png" />
            <p class="contentText">Nyt keskustelun korttien Phase 1 ja Phase 2 valinnat tallennetaan eri tauluun kuin
            Feedback korttien valinnat. Käyttäjän osoitetiedoille on luotu oma taulu.</p>
            <img class="sqlImage" src="../code/sql/databaseV3.png" />
            <p class="contentText">Deck taulu on nyt yhdistetty oikein License taulun kanssa. Keskustelun jokaisen vaiheen
            korttivalinnat tallennetaan omiin tauluihinsa. Card taulu on yhdistetty Deck tauluun ja keskustelun korttivalintojen
            tauluihin.</p>
            <img class="sqlImage" src="../code/sql/databaseV4.png" />
            <p class="contentText">Tietokannan viimeisimmän version MySQL koodi.</p>
            <p class="preFormatted" v-html="state.sqlcode"></p>  
        </div>
    </div>
</template>

<script>
import Prism from 'prismjs'

import 'prismjs/components/prism-bash'
import 'prismjs/components/prism-javascript'
import 'prismjs/components/prism-json'
import 'prismjs/components/prism-liquid'
import 'prismjs/components/prism-markdown'
import 'prismjs/components/prism-markup-templating'
import 'prismjs/components/prism-php'
import 'prismjs/components/prism-scss'

import 'prismjs/themes/prism.css'

import matterjs_html from 'raw-loader!../code/matterjs_html.txt';
import matterjs_js from 'raw-loader!../code/matterjs_js.txt';
import phase1backup1_html from 'raw-loader!../code/phase1backup1_html.txt';
import phase1backup1_js from 'raw-loader!../code/phase1backup1_js.txt';
import phase1demo2_html from 'raw-loader!../code/phase1demo2_html.txt';
import phase1demo2_js from 'raw-loader!../code/phase1demo2_js.txt';
import phase2demo2_html from 'raw-loader!../code/phase2demo2_html.txt';
import phase2demo2_js from 'raw-loader!../code/phase2demo2_js.txt';
import overlaydemo2_html from 'raw-loader!../code/overlaydemo2_html.txt';
import overlaydemo2_js from 'raw-loader!../code/overlaydemo2_js.txt';
import summarydemo2_html from 'raw-loader!../code/summarydemo2_html.txt';
import summarydemo2_js from 'raw-loader!../code/summarydemo2_js.txt';
import summaryanswer_html from 'raw-loader!../code/summaryanswer_html.txt';
import summaryanswer_js from 'raw-loader!../code/summaryanswer_js.txt';
import savepdfdemo2_html from 'raw-loader!../code/savepdfdemo2_html.txt';
import savepdfdemo2_js from 'raw-loader!../code/savepdfdemo2_js.txt';
import logindemo2_html from 'raw-loader!../code/logindemo2_html.txt';
import logindemo2_js from 'raw-loader!../code/logindemo2_js.txt';
import store from 'raw-loader!../code/store.txt';
import main from 'raw-loader!../code/main.txt';
import phase2old_html from 'raw-loader!../code/phase2old_html.txt';
import phase2old_js from 'raw-loader!../code/phase2old_js.txt';
import overlayold_html from 'raw-loader!../code/overlayold_html.txt';
import overlayold_js from 'raw-loader!../code/overlayold_js.txt';
import dbmethodsdemo2 from 'raw-loader!../code/dbmethodsdemo2.txt';
import backendrouterdemo2 from 'raw-loader!../code/backendrouterdemo2.txt';
import userservicedemo2 from 'raw-loader!../code/userservicedemo2.txt';
import sqlcode from 'raw-loader!../code/sql/database_code.txt';
import { reactive } from 'vue';

export default {
  name: 'Code',
    setup() {
        Prism.highlightAll()
        const state = reactive({
            visibleContent: 0,
            matterjs: null,
            phase1backup1: null,
            phase1demo2: null,
            overlaydemo2: null,
            summarydemo2: null,
            summaryanswer: null,
            savepdfdemo2: null,
            logindemo2: null,
            dbmethodsdemo2: null,
            backendrouterdemo2: null,
            userservicedemo2: null,
            phase2old: null,
            overlayold: null,
            main: null,
            store: null,
            sqlcode: null,
            });

        const addCode = (htmlCode, jsCode) => {
            const html = Prism.highlight(htmlCode, Prism.languages.html, 'html');
            const js = Prism.highlight(jsCode, Prism.languages.javascript, 'javascript');
            return html + js;
        }

        state.matterjs = addCode(matterjs_html, matterjs_js);
        state.phase1backup1 = addCode(phase1backup1_html, phase1backup1_js);
        state.phase1demo2 = addCode(phase1demo2_html, phase1demo2_js);
        state.phase2demo2 = addCode(phase2demo2_html, phase2demo2_js);
        state.overlaydemo2 = addCode(overlaydemo2_html, overlaydemo2_js);
        state.summarydemo2 = addCode(summarydemo2_html, summarydemo2_js);
        state.summaryanswer = addCode(summaryanswer_html, summaryanswer_js);
        state.savepdfdemo2 = addCode(savepdfdemo2_html, savepdfdemo2_js);
        state.logindemo2 = addCode(logindemo2_html, logindemo2_js);
        state.phase2old = addCode(phase2old_html, phase2old_js);
        state.overlayold = addCode(overlayold_html, overlayold_js);
        
        state.dbmethodsdemo2 = Prism.highlight(dbmethodsdemo2, Prism.languages.javascript, 'javascript');
        state.backendrouterdemo2 = Prism.highlight(backendrouterdemo2, Prism.languages.javascript, 'javascript');
        state.userservicedemo2 = Prism.highlight(userservicedemo2, Prism.languages.javascript, 'javascript');
        state.main = Prism.highlight(main, Prism.languages.javascript, 'javascript');
        state.store = Prism.highlight(store, Prism.languages.javascript, 'javascript');
        state.sqlcode = Prism.highlight(sqlcode, Prism.languages.javascript, 'javascript');

        const toggleVisibility = (index) => {
            state.visibleContent = index;
        }

        return { state, toggleVisibility };
    }

}


</script>

